import React, {Component} from 'react';
import {MyContext} from './ReactContext';
import axios from 'axios';
import {Link} from 'react-router-dom';
import {convertDateToDateAndTime} from '../utils/convertDateToDateAndTime';

class RemindersEdit extends Component {
  state = {
    _id: this.props.location.state.id,
    date: this.props.location.state.date,
    remindMe: this.props.location.state.remindMe,
    text: this.props.location.state.text,
    gridRadios: this.props.location.state.gridRadios,
    userId: this.context.state.userId,
    radioState: false,
  };

  componentDidMount () {
    // lisa premiumi olemasolemise rida
    const premium = this.context.state.premium
      ? this.context.state.premium
      : localStorage.getItem ('premium');
  }

  handleEntry = e => {
    console.log (e.target.value);
    let {name, value} = e.target;
    this.setState ({[name]: value});
  };

  handleFormSubmit = e => {
    e.preventDefault ();
    let id = this.props.location.state.id;
    axios
      .post (
        `/reminders/remindersedit`,
        this.state
        // {
        //   withCredentials: true,
        // }
      )
      .then (response => {
        console.log ('This comes back from remindersedit:', response.data);
        //update context state:
        // this.context.handleLogin (response);

        this.props.history.push ('/');
      })
      .catch (err => {
        this.setState ({error: err.response.message});
        console.log (err);
      });
  };

  render () {
    // const userId = this.context.state.userId;
    return (
      <MyContext.Consumer>
        {context => (
          <React.Fragment>

            {/* FORM */}
            <div className="pd-top">
              <h1>Edit Your Reminder</h1>
            </div>
            <div>
              <form onSubmit={this.handleFormSubmit}>

                <div className="form-group row">
                  <label htmlFor="date" className="col-sm-2 col-form-label">
                    Date and time
                  </label>
                  <div className="col-sm-10">
                    <p>
                      {convertDateToDateAndTime (this.state.date)}
                    </p>
                    <input
                      // defaultValue={this.state.date}
                      type="datetime-local"
                      name="date"
                      className="form-control"
                      id="date"
                      placeholder={convertDateToDateAndTime (this.state.date)}
                      onChange={e => this.handleEntry (e)}
                    />
                  </div>
                </div>

                {/* Remind me  */}
                <div className="form-group row">
                  <label htmlFor="remindme" className="col-sm-2 col-form-label">
                    Remind me :
                  </label>
                  <div
                    className="col-sm-10"
                    onChange={e => this.handleEntry (e)}
                  >
                    <p>
                      {this.state.remindMe}
                    </p>
                    <select
                      name="remindMe"
                      className="form-control"
                      id="remindme"
                    >
                      <option defaultValue={this.state.date}>
                        Please choose new reminder alert time:
                      </option>
                      <option value="6h">6 hours before</option>
                      <option value="3h">3 hours before</option>
                      <option value="1h">1 hour before</option>
                      <option value="25min">25 minutes before</option>
                      <option value="0min">At time of event</option>
                      <option value="none">Without reminder</option>
                    </select>
                  </div>
                </div>

                {/* Text field */}
                <div className="form-group row">
                  <label htmlFor="text" className="col-sm-2 col-form-label">
                    Reminder:
                  </label>
                  <div className="col-sm-10">
                    <input
                      defaultValue={this.state.text}
                      type="text"
                      name="text"
                      className="form-control"
                      id="text"
                      placeholder="Enter your reminder here"
                      onChange={e => this.handleEntry (e)}
                    />
                  </div>
                </div>

                {/* Radios */}
                <fieldset
                  className="form-group"
                  onChange={e => this.handleEntry (e)}
                >
                  <div className="row">
                    <legend className="col-form-label col-sm-2 pt-0">
                      Radios
                    </legend>
                    <div className="col-sm-10">
                      <div className="form-check">
                        <p />
                        <br />
                        <input
                          className="form-check-input"
                          type="radio"
                          name="gridRadios"
                          id="gridRadios1"
                          value="email"
                          defaultChecked={this.state.gridRadios === 'email'}
                        />
                        <label
                          className="form-check-label"
                          htmlFor="gridRadios1"
                        >
                          EMAIL reminder notification
                        </label>
                      </div>
                      {context.state.premium === 'Yes' &&
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="gridRadios"
                            id="gridRadios2"
                            value="sms"
                            defaultChecked={this.state.gridRadios === 'sms'}
                          />
                          <label
                            className="form-check-label"
                            htmlFor="gridRadios2"
                          >
                            SMS reminder notification
                          </label>
                        </div>}

                    </div>
                  </div>
                </fieldset>
                <div className="form-group row">
                  <div className="col-sm-10">
                    <button type="submit" className="btn btn-primary">
                      Submit EDIT
                    </button>
                  </div>
                </div>
              </form>
            </div>

            {/* <pre>state:{JSON.stringify (this.state, '\t', 2)}</pre> */}

          </React.Fragment>
        )}
      </MyContext.Consumer>
    );
  }
}
RemindersEdit.contextType = MyContext;

export default RemindersEdit;
